import React, { Component } from 'react';
import { Card, CardImg, CardImgOverlay, CardText, CardBody, CardTitle, Breadcrumb, BreadcrumbItem } from 'reactstrap';
import DishDetail from './DishDetailComponent';
import {Link} from 'react-router-dom';


class Menu extends Component{
    constructor(props){
        super(props);

        this.state= {
            selectedDish: null
        }
        console.log('Menu Component Constructor is created');
    }

    componentDidMount(){
        console.log('Menu component is Mounted');
    }
    onDishSelect(dish){
        this.setState({
            selectedDish: dish
        });
    }
    renderDish(dish){
        if(dish!=null){

            return(
                
                <div className="row">
             <Card className="col-12 col-md-5 mt-1">
                <CardImg width="50%"  className="col-12" src={dish.image} alt={dish.image}/>
                <CardBody className="col-12">
                    <CardTitle>{dish.name}</CardTitle>
                    <CardText>{dish.description}</CardText>  
                    
                </CardBody>
             </Card>
            
             <div className="col-12 col-md-5 mt-2">
                <div className="col-12">
                 <h4>Comments</h4>
                 <div>
                    <DishDetail value={dish.comments[0]}/>
                    <DishDetail value={dish.comments[1]}/>
                    <DishDetail value={dish.comments[2]}/>
                    <DishDetail value={dish.comments[3]}/>
                    <DishDetail value={dish.comments[4]}/>
                 </div>
                </div>
             </div>
             </div>

             
 
               
            )
        }
        else{
            return(
            <div></div>
            );
        }
    }
    
    render(){
        
        const menu = this.props.dishes.map((dish) => {
            return (
            
                
                <div key={dish.id} className="col-6 col-md-5 mt-1">
                    <Card onClick={()=>this.onDishSelect(dish)}>
                        <Link to={`/menu/${dish.id}`}>
                        <CardImg width="100%" height="300px" src={dish.image} alt={dish.image}/>
                        
                        <CardImgOverlay>
                            <CardTitle>{dish.name}</CardTitle>
                        </CardImgOverlay>
                        </Link>
                    </Card>
                </div>
            );
        });
        console.log('Menu component Render is invoked');
        return (
            
            <div className="container">
               <div className="row">
                   <Breadcrumb>
                       <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                       <BreadcrumbItem active>Menu</BreadcrumbItem>
                   </Breadcrumb>
                   <div className="col-12">
                       <h3>Menu</h3>
                   </div>
               </div>
                <div className='row'>
                    
                        {menu}

                </div>
                <div className="row">
                    {this.renderDish(this.state.selectedDish)}
                    
                    

                </div>
                <div>
                
                </div>
               
            </div>

        );
    }
}
export default Menu;